img[src=""] {
  opacity: 0;
}
.user-reply .user-reply-cell {
  /*justify-content:space-between;*/
  font-size: 0.28rem;
  padding: 0.3rem;
  border-bottom: 1px solid #ddd;
}
.user-reply .user-reply-cell a {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}
.user-reply .user-reply-cell a .user-reply-avatar {
      display: inline-block;
      width: 0.8rem;
      height: 0.8rem;
      margin-right: 0.3rem;
      border-radius: 0.4rem;
      overflow: hidden;
}
.user-reply .user-reply-cell a .user-reply-avatar img {
        width: 100%;
        height: 100%;
}
.user-reply .user-reply-cell a .user-reply-content {
      -webkit-box-flex: 1;
          -ms-flex: 1;
              flex: 1;
      overflow: hidden;
      /*width: 0;*/
}
.user-reply .user-reply-cell a .user-reply-content h3 {
        /*width:80%;*/
        height: 0.48rem;
        color: #333;
        overflow: hidden;
        -ms-text-overflow: ellipsis;
        text-overflow: ellipsis;
        white-space: nowrap;
}
.user-reply .user-reply-cell a .user-reply-content .user-reply-name {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        -webkit-box-pack: justify;
            -ms-flex-pack: justify;
                justify-content: space-between;
        font-size: 0.24rem;
        color: #555;
}



