img[src=""] {
  opacity: 0;
}
.user-header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  padding: 0.3rem 0 0.2rem;
  height: 4rem;
  background-image: url("../assets/images/user_bg.png");
  background-repeat: no-repeat;
  background-size: 100%;
  color: #fff;
}
.user-header .md-toolbar {
    background: transparent !important;
}
.user-header .md-toolbar a:not(.md-button) {
      color: #fff;
}
.user-header .avatar {
    /*margin-top:pr(25);*/
    width: 1.6rem;
    height: 1.6rem;
    border-radius: 0.8rem;
    overflow: hidden;
}
.user-header .avatar img {
      display: block;
      vertical-align: bottom;
      width: 100%;
      height: 100%;
}
.user-header .user-name {
    font-size: 0.32rem;
    text-align: center;
    /*margin-top:pr(8);*/
}
.user-header .user-mail {
    font-size: 0.28rem;
    /*margin-top:pr(8);*/
}
.user-header .user-mail a {
      color: #eee !important;
      text-decoration: underline;
}
.user-header .reg-info {
    width: 90%;
    /*margin-top:pr(15);*/
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}
.user-header .reg-info span {
      font-size: 0.24rem;
}
.user-header .reg-info span.score {
        color: #388e3c;
}
.user-nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 0.7rem;
  background-color: #333;
}
.user-nav .nav-title {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    font-size: 0.28rem;
    color: #ddd;
}
.user-nav .nav-title a {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-pack: center;
          -ms-flex-pack: center;
              justify-content: center;
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
      width: 100%;
      height: 100%;
      font-size: 0.24rem;
      box-sizing: border-box;
      color: #ddd !important;
}
.user-nav .nav-title a:hover {
        color: #fff !important;
        border-bottom: 0.04rem solid #388e3c;
}



