img[src=""] {
  opacity: 0;
}
img[src=""] {
  opacity: 0;
}
.message-list {
  padding-top: 1.12rem;
}
.message-cell {
  padding: 0.3rem;
  font-size: 0.32rem;
  border-bottom: 1px solid #d7d7d7;
}
.message-cell .msg-title {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}
.message-cell .msg-title .msg-user {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
}
.message-cell .msg-title a {
      display: inline-block;
      width: 0.88rem;
      height: 0.88rem;
      border-radius: 0.44rem;
      background-color: #ddd;
      overflow: hidden;
}
.message-cell .msg-title a img {
        width: 100%;
        height: 100%;
}
.message-cell .msg-title .msg-name {
      margin-left: 0.3rem;
}
.message-cell .msg-title .msg-name p:first-of-type {
        margin-top: 0.04rem;
}
.message-cell .msg-title .msg-name p:last-of-type {
        margin-top: 0.16rem;
        color: #888;
        font-size: 0.24rem;
}
.message-cell .msg-title .msg-time {
      margin-top: 0.1rem;
      font-size: 0.28rem;
      color: #888;
}
.message-cell .msg-content {
    font-size: 0.28rem;
    padding: 0.3rem 0 0.3rem;
    line-height: 1.5;
}
.message-cell .msg-topic {
    padding: 0.2rem 0.2rem;
    font-size: 0.24rem;
    color: #888;
    background-color: #ddd;
    overflow: hidden;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis;
    white-space: nowrap;
}



