
@font-face {
  font-family: 'iconfont';
  /* project id 311056 */
  src: url("//at.alicdn.com/t/font_e3r3kwh70n8hyqfr.eot");
  src: url("//at.alicdn.com/t/font_e3r3kwh70n8hyqfr.eot?#iefix") format("embedded-opentype"), url("//at.alicdn.com/t/font_e3r3kwh70n8hyqfr.woff") format("woff"), url("//at.alicdn.com/t/font_e3r3kwh70n8hyqfr.ttf") format("truetype"), url("//at.alicdn.com/t/font_e3r3kwh70n8hyqfr.svg#iconfont") format("svg");
}
img[src=""] {
  opacity: 0;
}
body {
  background-color: #f8f8f8 !important;
}
.login-header {
  height: 4.4rem;
  background-image: url("../assets/images/login_bg.png");
  background-repeat: no-repeat;
  background-size: 100%;
}
.login-header .md-toolbar {
    background: transparent !important;
}
.login-panel {
  width: 90%;
  /*height:pr(200);*/
  margin: 0 auto;
  position: relative;
  top: -1rem;
  padding: 0.6rem 0.4rem;
  font-size: 0.32rem;
  background: #fff;
  border: 1px solid #ccc;
  box-shadow: 0 2px 5px #ccc, 0 0 1px #ccc, 0 0 1px #ccc;
}
.login-panel.login-focus .login-label {
    color: #388e3c;
    -webkit-transform: scale(0.85, 0.85) translateY(-0.4rem);
            transform: scale(0.85, 0.85) translateY(-0.4rem);
}
.login-panel .error-info {
    margin-top: 0.1rem;
    font-size: 0.24rem;
    color: #f00;
}
.login-panel .login-label {
    position: absolute;
    font-size: 0.28rem;
    top: 0.7rem;
    z-index: 0;
    -webkit-transform-origin: 0 0;
    transform-origin: 0 0;
    transition-duration: .3s;
    transition-property: color, -webkit-transform;
    transition-property: color, transform;
    transition-property: color, transform, -webkit-transform;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}
.login-panel .token {
    display: block;
    width: 100%;
    border: none;
    padding: 0.1rem 0;
    border-bottom: 2px solid #388e3c;
    z-index: 1;
}
.login-panel .token.error-info-token {
      border-bottom-color: #f00;
}
.login-panel .token:focus {
      outline: none;
}
.login-panel .login-btn {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    width: 100%;
    height: 0.7rem;
    font-size: 0.28rem;
    background-color: #388e3c;
    color: #fff;
    border: none;
    margin-top: 0.4rem;
    border-radius: 0.04rem;
}
.login-panel .login-option {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    margin-top: 0.4rem;
    padding: 0 0.2rem;
    font-size: 0.28rem;
}
.login-panel .login-option .iconfont {
      font-family: "iconfont";
      font-size: 0.28rem;
      font-style: normal;
      -webkit-font-smoothing: antialiased;
      -webkit-text-stroke-width: 0.2px;
      -moz-osx-font-smoothing: grayscale;
      margin-right: 0.2rem;
}



